.footer{
  padding: 3.5em 0;
  background-color: $black;
  position: relative;
  z-index: 2;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 12.5625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    max-width: 22.3125em;
    margin-top: 1.5em;
  }
  &__reliable{
    opacity: .5;
    line-height: 160%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1em;
  }
  &__link{
    margin-top: .25em;
    font-size: .9375em;
    line-height: 150%;
    letter-spacing: -0.02em;
    color: rgba(255, 255, 255, 0.3);
  }
  &__title{
    font-weight: 800;
    font-size: 1.375em;
    line-height: 160%;
  }
}

.footer-nav{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    font-size: .9375em;
    line-height: 160%;
    opacity: .5;
    margin-top: .875em;
    display: flex;
    align-items: center;
    img{
      width: 1.53em;
      margin-right: .67em;
    }
    &:hover{
      opacity: 1;
      color: $white;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__inner{
      @include grid100gap(1.5em);
      position: relative;
      padding-top: 3em;
    }
    &__col1{
      order: 2;
    }
    &__logo{
      width: 8em;
      position: absolute;
      top: 0;
      left: 0;
    }
    &__title{
      font-size: 1.25em;
    }
    &__info{
      max-width: unset;
      margin: 0;
    }
  }

  .footer-nav{
    &__link{
      font-size: .875em;
    }
  }
}


