.about{
  padding-bottom: 1.5625em;
  @media screen and (min-width: 993px){
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 23.125em;
      background: #2b2b2b;
    }
    .container{
      padding: 0 3.75em;
    }
    .h1{
      line-height: 100%;
    }
  }
  &__inner{
    padding: 6em 6em 6.75em 6.875em;
    background-color: $black;
    border-radius: 16em 0 0 0;
    position: relative;
    z-index: 2;
    display: flex;
    border-bottom: 1px solid #303030;
  }
  &__col{
    padding-top: 3.3125em;
    width: 33.125em;
    margin-right: 6em;
  }
  &__logo{
    width: 20.6875em;
    max-width: 100%;
    display: block;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
}

.main{
  position: relative;
  &__video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding: 10em 5em 19.75em;
  }
  &__content{
    width: 28.625em;
    max-width: 100%;
    margin-left: auto;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
  &__btn{
    margin-right: 1.375em;
    &:last-child{
      margin: 0;
    }
    img{
      margin-left: .625em;
      width: .75em;
    }
    svg{
      width: 1.0625em;
      margin-left: .5em;
    }
    &--big-img{
      img{
        width: 1.5625em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__inner{
      padding: 2em 0;
    }
    &__col{
      margin-right: 3em;
    }
    &__logo{
      width: 15em;
    }
  }

  .main{
    &__inner{
      padding: 3em 0;
    }
    &__content{
      margin-left: 0;
    }
    &__desc{
      font-size: .875em;
      margin-top: 1em;
    }
    &__nav{
      margin-top: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__inner{
      flex-direction: column;
    }
    &__col{
      width: 100%;
      order: 2;
      margin: 1em 0 0;
      padding: 0;
    }
    &__logo{
      width: 10em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .main{
    &__nav{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .5em;
    }
  }
}
