.stat{
  &__inner{
    position: relative;
    margin-top: -12em;
  }
  &__triangle{
    position: absolute;
    right: 0;
    bottom: 4em;
    width: 8em;
  }
  &__content{
    padding: 3em 7em 3.75em 3.3125em;
    border: 1px solid #303030;
    background-color: $black;
    width: 67em;
    max-width: 100%;
    margin-left: -2.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 1.625em;
      height: 100%;
      background-color: $primary;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .5em .75em;
  }
  &__links{
    padding-top: 1.6875em;
    display: flex;
    flex-wrap: wrap;
    margin-top: 2.8125em;
    width: calc(100% + 1em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 39.5em;
      height: 1px;
      background-color: #303030;
      top: 0;
      left: -3.3125em;
    }
  }
  &__link{
    margin: .9375em .6875em 0 0;
    padding: 0 1.125em;
    height: 2.125em;
    @include center;
    border: 1px solid #303030;
    border-radius: 3.1875em;
    color: $white;
    span{
      font-size: .875em;
    }
    &:hover{
      background-color: $primary;
      border-color: $primary;
      color: $black;
    }
  }
}

.stat-item{
  display: grid;
  grid-template-columns: 16.25em calc(100% - 17.25em);
  gap: 1em;
  align-items: center;
  @media screen and (min-width: 993px){
    &._type2{
      grid-template-columns: 12.75em calc(100% - 13.75em);
      .h1{
        text-align: right;
      }
    }
  }
}

.nav-slider{
  &._small{
    @media screen and (min-width: 993px){
      .swiper-slide{
        width: 20%;
      }
    }
  }
  &._small2{
    @media screen and (min-width: 993px){
      .swiper-slide{
        width: 25%;
      }
    }
  }
}

.catalog-items{
  &._type2{
    @media screen and (min-width: 993px){
      .catalog-card__img{
        margin-bottom: 1.875em;
      }
    }
  }
}

.catalog2{
  padding-top: 3em;
}

.help{
  padding: 2.625em 0 9.75em;
  &__top{
    text-align: center;
    width: 62em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(2.25em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.75em;
    .main__btn{
      margin: 0 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .stat{
    &__inner{
      margin-top: 0;
    }
    &__triangle{
      display: none;
    }
    &__content{
      overflow: hidden;
      padding: 1em;
      width: 100%;
      margin-left: 0;
      &:before{
        width: .5em;
        z-index: 2;
      }
    }
    &__links{
      margin-top: 1em;
      padding-top: .25em;
      &:before{
        width: calc(100% + 2em);
        left: -1em;
      }
    }
    &__link{
      margin: .5em .125em 0 0;
      height: 1.5em;
      padding: 0 .75em;
      span{
        font-size: .75em;
      }
    }
  }

  .stat-item{
    grid-template-columns: 7em calc(100% - 8em);
    .text16{
      font-size: .75em;
    }
  }

  .help{
    padding-bottom: 4em;
    &__top{
      gap: 1em;
    }
    &__nav{
      margin-top: 1.5em;
      .main__btn{
        margin: 0 .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .stat{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .help{
    &__nav{
      display: block;
      .main__btn{
        margin: 0 0 1em;
      }
    }
  }
}

