.why{
  padding: 5.25em 0 3.5em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 23.125em;
    background: #2b2b2b;
  }
  &:after{
    content: '';
    position: absolute;
    height: 100%;
    width: calc(100% - 7.5em);
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    background-color: $black;
    border-radius: 9.375em 9.375em 0 0;
  }
  &__inner{
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: calc(100% - 40.6875em) 36.0625em;
    gap: 3.625em;
  }
  &__img{
    width: calc(100% + 6.25em);
    margin-left: -6.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__col2{
    padding-top: .625em;
    padding-right: 1.5em;
    max-width: 33.125em;
  }
  .m-title{
    margin-bottom: 1.5em;
  }
  &__desc{
    margin-top: 1.25em;
  }
  &__list{
    width: calc(100% + .8125em);
    padding: .3125em 0 0 0;
    &-item{
      padding: 1.25em 0 1.25em 1.5625em;
      display: flex;
      align-items: flex-start;
      font-size: 1em;
      line-height: 160%;
      border-bottom: 1px solid #303030;
      &:before{
        content: '';
        flex-shrink: 0;
        width: .625em;
        height: .5em;
        background-image: url(../img/arrow2.svg);
        @include bgImgSize(100%);
        margin: .625em 1.875em 0 0;
      }
      &:last-child{
        border-bottom: none;
      }
    }
  }
}

.catalog{
  padding: 1.5625em 0 4.625em;
  &__top{
    display: flex;
    justify-content: center;
    margin-bottom: 1.625em;
  }
}

.nav-slider{
  padding: 0 3.25em;
  margin-top: 1.875em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1px);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding: 0 1.625em;
      width: calc(100% / 7);
      min-height: 3.125em;
      @include center;
      border-right: 1px solid $white;
      text-align: center;
    }
  }
  &__link{
    font-size: 1em;
    line-height: 160%;
  }
}

.catalog-items{
  margin-top: 3.5em;
  column-count: 3;
  column-gap: 2.8125em;
}

.catalog-card{
  -webkit-column-break-inside: avoid;
  -webkit-backface-visibility: hidden;
  -moz-page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
  break-inside: avoid;
  -o-column-break-inside: avoid;
  -ms-column-break-inside: avoid;
  column-break-inside: avoid;
  page-break-inside: avoid;
  display: block;
  width: 100%;
  margin-bottom: 3em;
  position: relative;
  &:last-child{
    margin-bottom: 5em;
  }
  &__badge{
    position: absolute;
    top: 1.875em;
    left: 2em;
    border: 1px solid #040007;
    height: 2em;
    display: flex;
    align-items: center;
    background-color: $primary;
    color: $white;
    padding: 0 1.5em;
    font-weight: 500;
    z-index: 2;
    border-radius: 3.1875em;
    span{
      font-size: .75em;
    }
  }
  &__img{
    margin-bottom: 2.625em;
    position: relative;
    display: block;
    &:hover{
      &:before{
        border-color: $primary;
      }
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid rgba(255, 255, 255, 0.1);
      transition: all .3s;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: .625em;
  }
  &__nav{
    display: flex;
    margin-top: .625em;
  }
}

.m-link{
  display: flex;
  align-items: center;
  font-weight: 600;
  color: $primary;
  text-transform: uppercase;
  &:hover{
    opacity: .8;
  }
  svg{
    width: .75em;
    margin-left: .3125em;
  }
  span{
    font-size: 1.0625em;
    line-height: 130%;
  }
}


@media screen and (max-width: 992px){
  .why{
    padding: 2em 0;
    &:before,
    &:after{
      display: none;
    }
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__img{
      margin: 0;
      width: 100%;
      position: sticky;
      top: 1em;
    }
    &__col2{
      padding: 0;
    }
    &__desc,
    &__list{
      font-size: .875em;
    }
    .m-title{
      margin-bottom: .75em;
    }
  }

  .catalog{
    padding-bottom: 2em;
    overflow: hidden;
    &__top{
      margin-bottom: 1em;
      justify-content: unset;
    }
    .h1{
      text-align: left;
    }
  }

  .nav-slider{
    padding: 0;
    .swiper-button{
      display: none;
    }
    .swiper-slide{
      width: 9.5em;
      padding: 0 .5em;
    }
    .slider__wrap{
      overflow: unset;
    }
    &__link{
      font-size: .75em;
      text-align: left;
    }
  }

  .catalog-items{
    column-count: 2;
    margin-top: 2em;
  }

  .catalog-card{
    &__img{
      margin-bottom: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .why{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .catalog-items{
    column-count: 1;
  }

  .catalog-card{
    font-size: .75em;
    margin-bottom: 2em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}
