._z2{
  position: relative;
  z-index: 2;
  background-color: $black;
}

.partners,
.help{
  background-image: url(../img/partners-bg.svg);
  background-size: 100% auto;
  background-position: left bottom;
  background-repeat: no-repeat;
}

.partners{
  padding: 1.625em 0 6.75em;
  overflow: hidden;
  &__top{
    display: flex;
    justify-content: center;
  }
}

.m-title{
  display: flex;
  align-items: center;
  color: $primary;
  font-weight: 600;
  text-transform: uppercase;
  &:before{
    content: '';
    width: 1.375em;
    height: 1px;
    background-color: $primary;
    margin-right: 1.5em;
  }
  span{
    letter-spacing: 0.05em;
  }
}

.swiper-button{
  width: 3.125em;
  @include center;
  &-prev{
    left: 0;
  }
  &-next{
    right: 0;
  }
  svg{
    width: 100%;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
}

.partners-slider{
  margin-top: 4.125em;
  padding: 0 5.25em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-wrapper{
      align-items: center;
    }
    &-slide{
      display: flex;
      align-items: center;
      width: auto;
      padding-right: 3.5em;
    }
  }
}

.partner-card{
  img{
    max-width: 17.25em;
    max-height: 6.5em;
  }
}

.advantages{
  padding: 3.5em 0 1.625em;
  &__top{
    margin-bottom: 1.625em;
  }
  &__items{
    margin-top: 4.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em));
    gap: 7.5em;
  }
}

.advantage-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 6.25em;
    @include center;
    margin-bottom: 1.75em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .m-title{
    &:before{
      margin-right: .75em;
    }
  }

  .partners{
    padding-bottom: 4em;
    &__top{
      justify-content: unset;
    }
  }

  .partners-slider{
    padding: 0;
    margin-top: 2em;
    .swiper-button{
      display: none;
    }
    .swiper-slide{
      padding-right: 2em;
    }
    .slider__wrap{
      overflow: unset;
    }
  }

  .partner-card{
    img{
      display: block;
      max-width: 12em;
      max-height: 3em;
    }
  }

  .advantages{
    &__top{
      justify-content: unset;
    }
    .h1{
      text-align: left;
    }
    &__items{
      margin-top: 2.5em;
      grid-template-columns: repeat(3,calc(100% / 3 - 4em / 3));
      gap: 2em;
    }
  }

  .advantage-card{
    font-size: .75em;
    text-align: left;
    display: block;
    &__icon{
      height: 4em;
      justify-content: unset;
      margin-bottom: 1em;
    }
    &__desc{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
