@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.375em;
    border-radius: 3.125em;
    font-family: $font2;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.0625em;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-red-outline{
    border: 1px solid $primary;
    color: $white;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}
