@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    background-color: $black;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $white;
    font-size: calc(1440px * (16 / 1440));
    @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
    }
    &:before,
    &:after{
        content: '';
        position: fixed;
        width: 1px;
        height: $vh;
        background-color: #303030;
        top: 0;
    }
    &:before{
        left: 3.75em;
    }
    &:after{
        right: 3.75em;
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 6.25em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
        &:before,
        &:after{
            display: none;
        }
    }

    .container{
        padding: 0 1em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 360));
    }
}


