.header{
  border-bottom: 1px solid #303030;
  &__inner{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: calc(100% + 2.5em);
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    margin-bottom: 2.375em;
    width: 11.8125em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.mob-menu{
  position: relative;
}

.nav{
  display: flex;
  align-items: flex-end;
  &__link{
    background: rgba(255, 255, 255, 0.05);
    margin-left: 1px;
    height: 9.375em;
    width: 6em;
    padding: 5.375em .5625em 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: right;
    overflow: hidden;
    position: relative;
    &:after,
    &:before{
      content: '';
      transition: all .3s;
    }
    &:after{
      position: absolute;
      left: 50%;
      top: 5em;
      width: calc(100% - .875em);
      height: 1.5px;
      transform: translateX(-50%);
      background-color: rgba(255, 255, 255, 0.2);
    }
    &:before{
      width: .6875em;
      height: 1em;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='11' height='16' viewBox='0 0 11 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.5 2.82715L5.5 13.8271' stroke='white' stroke-linecap='round'/%3E%3Cpath d='M10 9.6543V9.6543C8.4018 10.7536 7.04748 12.1707 6.02168 13.817L5.5 14.6543L5.25863 14.1894C4.28472 12.3137 2.81079 10.7441 1 9.6543V9.6543' stroke='white' stroke-linecap='round'/%3E%3C/svg%3E");
      @include bgImgSize(100%);
      order: 2;
      margin: .1875em 0 0 auto;
      opacity: .25;
      flex-shrink: 0;
    }
    span{
      font-size: .75em;
      line-height: 130%;
    }
    &._active{
      pointer-events: none;
      color: $black;
      background-color: $primary;
      &:after{
        background-color: $black;
      }
      &:before{
        opacity: 1;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='11' height='16' viewBox='0 0 11 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.5 2.82715L5.5 13.8271' stroke='black' stroke-linecap='round'/%3E%3Cpath d='M10 9.6543V9.6543C8.4018 10.7536 7.04748 12.1707 6.02168 13.817L5.5 14.6543L5.25863 14.1894C4.28472 12.3137 2.81079 10.7441 1 9.6543V9.6543' stroke='black' stroke-linecap='round'/%3E%3C/svg%3E");
      }
    }
    &._not-arrow{
      &:before{
        display: none;
      }
    }
    &._big{
      width: 7.5em;
    }
  }
  &__link{
    @media screen and (min-width: 993px){
      &._small-height{
        margin-top: 5em;
        padding-top: .375em;
        height: 4.375em;
        &:after{
          display: none;
        }
      }
    }
  }
}

._flex,
._flex-center{
  display: flex;
  align-items: center;
}

._flex-center{
  justify-content: center;
}

.header-contacts{
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  @media screen and (min-width: 993px){
    padding: 1.625em .875em 0 0;
    height: 5.375em;
  }
  &__link{
    display: flex;
    align-items: center;
    opacity: .5;
    margin-left: 1em;
    &:hover{
      color: $white;
      opacity: 1;
    }
    img{
      width: 1.375em;
      margin-right: .5em;
    }
    span{
      font-size: .75em;
      line-height: 160%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.5em 0;
    &__inner{
      width: 100%;
    }
    &__logo{
      margin: 0;
      width: 8em;
      position: relative;
      z-index: 21;
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .mob-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    padding: 5em 1em 1em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    z-index: 20;
    background-color: $black;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      padding: 0;
      width: auto !important;
      height: auto;
      justify-content: center;
      background-color: transparent !important;
      margin-bottom: 1em;
      &:before,
      &:after{
        display: none;
      }
      &:hover,
      &._active{
        color: $primary;
      }
    }
  }

  .header-contacts{
    position: static;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin: 1.5em 0 0;
    }
  }

  .burger{
    flex-shrink: 0;
    display: flex;
    padding: 0;
    width: 1.5em;
    height: 1.5em;
    position: relative;
    z-index: 21;
    ._view2{
      opacity: 0;
    }
    img{
      @include centerAbsolute;
      width: 100%;
      transition: all .3s;
    }
  }
}


